<template>
  <div class="content">
    <div class="content-wrap">
      <div class="sb-form form">
        <div class="form-item">
          <div class="form-item-name">
            <span>{{ $t("monitor.hzxx") }}：</span>
          </div>
          <div class="form-item-value">
            <span>{{ transData.patientName }} {{ transData.caseNum }}</span>
          </div>
        </div>
        <div class="form-item">
          <div class="form-item-name">
            <span>{{ $t("monitor.ysblx") }}：</span>
          </div>
          <div class="form-item-value">
            <el-select
              class="select"
              style="width: 145px"
              v-model="changemachine"
              filterable
              @change="selectOld"
              :placeholder="$t('message.select')"
            >
              <el-option
                v-for="item in oldmachineList"
                :key="item.machineId"
                :label="item.typeName"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="form-item">
          <div class="form-item-name">
            <span>{{ $t("monitor.ysbbh") }}：</span>
          </div>
          <div class="form-item-value">
            <span class="value-span"
              ><i>{{ oldMachineNo }}</i
              ><span v-if="oldMachineNo && oldMachineSn">|</span>
              {{ oldMachineSn }}</span
            >
          </div>
        </div>
        <div class="form-item">
          <div class="form-item-name">
            <span>{{ $t("monitor.xsbbh") }}：</span>
          </div>
          <div class="form-item-value">
            <el-select
              class="select"
              style="width: 145px"
              v-model="newmachineId"
              @change="getNewMachine"
              filterable
              :placeholder="$t('message.select')"
            >
              <el-option
                v-for="item in newmachineList"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </div>
      <div class="form-img">
        <img
          src="../../assets/img/device/lc.png"
          alt=""
          v-if="oldMachineType === 3 && machineTypeIdChild === 11"
        />
        <img
          src="../../assets/img/device/htjr.png"
          alt=""
          v-if="oldMachineType === 3 && machineTypeIdChild === 15"
        />
        <img
          src="../../assets/img/monitor/case1.png"
          alt=""
          v-if="oldMachineType === 0 && language == 'zh'"
        />
        <img
          src="../../assets/img/device/xindian.png"
          alt=""
          v-if="oldMachineType === 1"
          :style="{ marginLeft: '26px', marginTop: '-84px' }"
        />
        <!-- <img src="../../assets/img/device/xy.png" alt="" v-if="oldMachineType===2"> -->
        <img
          src="../../assets/img/monitor/case4.png"
          alt=""
          v-if="oldMachineType === 2 && machineTypeIdChild == 10"
        />
        <img
          src="../../assets/img/device/xy02.png"
          alt=""
          v-if="oldMachineType === 2 && machineTypeIdChild == 12"
        />
        <img
          src="../../assets/img/monitor/case-en.png"
          alt=""
          v-if="oldMachineType === 0 && language == 'en'"
        />
      </div>

    </div>
    <div class="ipt-group clearfix">
      <a href="javascript:void(0)" class="on" @click="chooseBedType()">{{
        $t("button.confirm")
      }}</a>
      <a href="javascript:void(0)" class="off" @click="showSet()">{{
        $t("button.close")
      }}</a>
    </div>
  </div>

</template>

<script>
import { machineList, changeMachine } from "@/api/monitor";
import popUp from "../../components/popUp";
import { mapGetters, mapActions } from "vuex";
var that = {};
export default {
  name: "ghsb",
  data() {
    return {
      changemachine: "",
      newmachineId: "",
      newmachineList: [],
      oldmachineList: [],
      oldMachineNo: "",
      oldMachineSn: "",
      oldMachineType: "",
      language: that.$store.state.login.language,
    };
  },
  props: ["ghsbStatus", "transData"],
  beforeCreate() {
    that = this;
  },
  mounted: function () {
    this.selectOld(this.oldmachineList[0]);
  },
  methods: {
    ...mapActions("monitor", ["refreshData"]),
    chooseBedType() {
      if (this.newmachineId) {
        changeMachine({
          baseId: this.transData.baseId,
          type: this.oldMachineType,
          machineId: this.newmachineId,
        }).then((res) => {
          console.log(res);
          if (res.code == 200 && res.success) {
            that.$emit("closeIitem", "更换入口");
            that.refreshData();
            that.success('更换成功');
          } else {

          }
        });
      }
    },
    showSet() {
      this.$emit("closeIitem", "更换入口");
    },
    getNewMachine(value) {
      console.log(value);
      that.newmachineList.map(function (item, index) {
        if (item.id == value) {
          that.machineTypeIdChild = item.machineTypeIdChild;
        }
      });
    },
    selectOld(data) {
      console.log(data);
      this.oldMachineNo = data.machineNo;
      this.oldMachineSn = data.machineSn;
      var typeName = "";
      if (data.type === 0) {
        if (this.language == "zh") {
          typeName = "体温贴";
        } else {
          typeName = "Temperature patch";
        }
      } else if (data.type === 1) {
        if (this.language == "zh") {
          typeName = "心电贴";
        } else {
          typeName = "ECG patch";
        }
      } else if (data.type === 2) {
        if (this.language == "zh") {
          typeName = "血氧仪";
        } else {
          typeName = "Oximeter";
        }
      } else if (data.type === 3) {
        if (this.language == "zh") {
          typeName = "离床感应";
        } else {
          typeName = "Off-bed inductor";
        }
      }
      this.changemachine = typeName;
      console.log(this.changemachine);
      this.newmachineList = [];
      this.newmachineId = "";
      this.oldMachineType = data.type;
      machineList({
        machineTypeId: data.machineTypeId,
        departmentId: this.departmentId,
      }).then((res) => {
        console.log(res);
        if (res.code == 200 && res.success) {
          this.newmachineList = res.data;
        }
      });
    },
  },
  components: {
    PopUp: popUp,
  },
  computed: {
    ...mapGetters("monitor", ["departmentId"]),
  },
  created() {
    console.log(this.transData);
    this.oldmachineList = this.transData.machineList.filter((item) => {
      return item.usageState === 1;
    });
    this.oldmachineList.map((item, index) => {
      if (item.type === 0) {
        if (this.language == "zh") {
          this.oldmachineList[index].typeName = "体温贴";
        } else {
          this.oldmachineList[index].typeName = "Temperature patch";
        }
      } else if (item.type === 1) {
        if (this.language == "zh") {
          this.oldmachineList[index].typeName = "心电贴";
        } else {
          this.oldmachineList[index].typeName = "ECG patch";
        }
      } else if (item.type === 2) {
        if (this.language == "zh") {
          this.oldmachineList[index].typeName = "血氧仪";
        } else {
          this.oldmachineList[index].typeName = "Oximeter";
        }
      } else if (item.type === 3) {
        if (this.language == "zh") {
          this.oldmachineList[index].typeName = "离床感应";
        } else {
          this.oldmachineList[index].typeName = "Off-bed inductor";
        }
      }
    });
  },
};
</script>
<style scoped lang="scss">
.content {
  margin: 0 42px 46px;
  .content-wrap{
    display:flex;
    .sb-form {
      width: 481px;
      float: left;
      // padding-bottom: 20px;
      .form-item {
        display: flex;
        height: 36px;
        margin-top: 20px;
        align-items: center;
        .form-item-name {
          width: 41%;
          span {
            font-size: 16px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: rgba(51, 51, 51, 1);
            float: right;
            // display:block;
            // line-height:36px;
            display: flex;
            justify-content: center;
            align-items: center;
          }
        }
        .form-item-value {
          width: 59%;
          display: flex;
          align-items: center;
          .value-span {
            line-height: normal;
            display: flex;
            align-items: center;
          }
          span {
            font-size: 16px;
            font-family: PingFangSC-Medium, PingFang SC;
            font-weight: 500;
            color: rgba(51, 51, 51, 1);
            float: left;
            display: block;
            i {
              font-style: normal;
              color: #1890ff;
            }
          }
          .select {
            float: left;
          }
        }
      }
    }
    .form-img {
      float: left;
      margin-right: 113px;
      display: flex;
      align-items: center;
      width: 202px;
      img {
        width: 202px;
        float: left;
      }
    }
  }

}
.form-item-value /deep/ .el-input__inner {
  height: 36px;
}
</style>



