<template>
  <div>
    <div class="header">
      <div class="header-left">
        <i class="iconfont font-yichang">&#xe636;</i>
        <div class="header-font">{{ $t("monitor.yichang.tishi") }}</div>
      </div>
      <div class="header-right" @click="showWarn">
        <i class="iconfont font-baojing">&#xe63c;</i>
        <i class="iconfont font-jingyong" v-if="baojing">&#xe64e;</i>
      </div>
    </div>
    <div class="content">
      <div class="content-warp">
        <div class="content-header">
          {{ $t("monitor.yichang.dangqian") }}：{{ number }}
          {{ $t("monitor.yichang.ren") }}
        </div>
        <audio src="" id="eventAudio"></audio>
        <div class="content-header"></div>
        <div class="content-item-warp">
          <div
            class="content-item"
            v-for="(item, index) in showData"
            :key="index"
          >
            <div class="item-left">
              <div
                class="item-mark"
                :class="item.yichang === 2 ? 'redMark' : ''"
              >
                {{ item.tiwen }}
              </div>
              <div class="item-message">{{ item.chuanghao }}</div>
              <div class="item-name">{{ item.name }}</div>
              <div
                class="item-template"
                :class="item.yichang === 2 ? 'red' : ''"
              >
                {{ item.temperature }}
              </div>
            </div>
            <div class="item-right">{{ item.time }}</div>
          </div>
        </div>
        <div class="page-warp">
          <div class="page">
            <div class="page-left" @click="up">
              <i class="iconfont font-page">&#xe627;</i>
            </div>
            <div class="page-center">{{ page }}</div>
            <div class="page-left" @click="next">
              <i class="iconfont font-page">&#xe628;</i>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import audio from "@/assets/music/tip.wav";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      showData: [],
      page: 1,
      pageStart: 0,
      pageEnd: 10,
      baojing: true,
      warnTime: "",
      buttonAudio: "",
      sheshi: " ℃"
    };
  },
  created() {
    this.showData = this.yiData.slice(this.pageStart, this.pageEnd);
    if (!this.selectTem) {
      this.sheshi = " ℉";
    }
  },
  computed: {
    ...mapState("monitor", ["mainData", "selectTem"]),
    number() {
      var data = this.mainData.monitorVOList;
      var numbers = 0;
      for (var i = 0; i < data.length; i++) {
        if (
          data[i].monitorBloodVO.alert === 1 ||
          data[i].monitorRespiratoryRateVO.alert === 1 ||
          data[i].monitorHeartRateVO.alert === 1 ||
          data[i].monitorTemperatureVO.alert === 1
        ) {
          numbers = numbers + 1;
        }
      }
      return numbers;
    },
    yiData: {
      get() {
        var data = this.mainData.monitorVOList;
        var arr = [];
        for (var i = 0; i < data.length; i++) {
          if (data[i].monitorBloodVO.alert === 1) {
            //血样
            var objPush = {
              tiwen: this.$t("monitor.yichang.xueyang"),
              chuanghao: data[i].bedName,
              name: data[i].patientName,
              temperature: data[i].monitorBloodVO.bloodOxygen + "%",
              time: "",
              yichang: data[i].monitorBloodVO.bloodOxygenAlert
            };
            arr.push(objPush);
          }
          if (data[i].monitorRespiratoryRateVO.alert === 1) {
            //呼吸率
            var objPush = {
              tiwen: this.$t("monitor.yichang.huxi"),
              chuanghao: data[i].bedName,
              name: data[i].patientName,
              temperature: data[i].monitorRespiratoryRateVO.respiratory + "bpm",
              time: "",
              yichang: data[i].monitorRespiratoryRateVO.respiratoryAlert
            };
            arr.push(objPush);
          }
          if (data[i].monitorHeartRateVO.alert === 1) {
            //心率
            var objPush = {
              tiwen: this.$t("monitor.yichang.xinlv"),
              chuanghao: data[i].bedName,
              name: data[i].patientName,
              temperature: data[i].monitorHeartRateVO.heart + "bpm",
              time: "",
              yichang: data[i].monitorHeartRateVO.heartAlert
            };
            arr.push(objPush);
          }
          if (data[i].monitorTemperatureVO.alert === 1) {
            //体温
            var objPush = {
              tiwen: this.$t("monitor.yichang.tiwen"),
              chuanghao: data[i].bedName,
              name: data[i].patientName,
              temperature:
                data[i].monitorTemperatureVO.temperature + this.sheshi,
              time: "",
              yichang: data[i].monitorTemperatureVO.temperatureAlert
            };
            arr.push(objPush);
          }
        }
        return arr;
      },
      set(newValue) {
        console.log(newValue);
      }
    }
  },
  watch: {
    yiData(newData, oldData) {
      console.log(newData, oldData);
      if (newData.length > 0) {
        this.showData = newData.slice(this.pageStart, this.pageEnd);
      }
      if (newData.length == 0) {
        this.showData = [];
      }
    },
    selectTem(o) {
      if (o) {
        this.sheshi = "  ℃";
      } else {
        this.sheshi = "  ℉";
      }
    }
  },
  methods: {
    playAudio() {
      var that = this;
      this.buttonAudio = document.getElementById("eventAudio");
      this.buttonAudio.setAttribute("src", audio);
      this.buttonAudio.setAttribute("loop", "loop");
      this.buttonAudio.play();
      setTimeout(function() {
        that.buttonAudio.pause();
      }, 9000);
    },
    next() {
      if (this.page * 10 < this.yiData.length) {
        this.pageStart = this.pageStart + 10;
        this.pageEnd = this.pageEnd + 10;
        this.page = this.page + 1;
        this.showData = this.yiData.slice(this.pageStart, this.pageEnd);
      }
    },
    up() {
      if (this.page != 1) {
        this.pageStart = this.pageStart - 10;
        this.pageEnd = this.pageEnd - 10;
        this.page = this.page - 1;
        this.showData = this.yiData.slice(this.pageStart, this.pageEnd);
      }
    },
    alarm() {
      if (this.number > 0 && this.baojing === false) {
        this.playAudio();
      }
    },
    showWarn() {
      this.baojing = !this.baojing;
      if (this.baojing == false) {
        this.alarm();
        this.setWarnInter();
      } else {
        if (this.buttonAudio) {
          this.buttonAudio.pause();
        }
        clearInterval(this.warnTime);
      }
    },
    setWarnInter() {
      //设置警报的循环
      this.warnTime = setInterval(this.alarm, 480000);
    }
  },
  destroyed() {
    clearInterval(this.warnTime);
  }
};
</script>
<style scoped lang="scss">
@mixin font-color() {
  color: rgba(255, 255, 255, 0.85);
  [data-theme="black"] & {
    color: rgba(255, 255, 255, 0.85);
  }
  [data-theme="white"] & {
    color: rgba(24, 76, 153, 1);
  }
}
@mixin background_color2() {
  background: rgba(7, 34, 74, 1);
  [data-theme="black"] & {
    background: rgba(7, 34, 74, 1);
  }
  [data-theme="white"] & {
    background: rgba(19, 74, 155, 1);
  }
}
@mixin background_color() {
  [data-theme="white"] & {
    background: rgba(248, 251, 255, 1);
  }
}
@mixin border() {
  border: 1px solid rgba(9, 76, 138, 1);
  [data-theme="black"] & {
    border: 1px solid rgba(9, 76, 138, 1);
  }
  [data-theme="white"] & {
    border: none;
  }
}
.header {
  width: 330px;
  height: 40px;
  @include background_color2();
  box-shadow: 1px 1px 5px 0px rgba(53, 83, 228, 1);
  border-radius: 5px 5px 0px 0px;
  border: 1px solid rgba(53, 83, 228, 1);
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-left {
  margin-left: 28px;
  display: flex;
}
.font-yichang {
  font-size: 21px;
  color: rgba(63, 201, 255, 1);
}
.header-font {
  font-size: 18px;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  line-height: 25px;
  letter-spacing: 1px;
  margin-left: 12px;
}
.font-baojing {
  font-size: 26px;
  margin-right: 33px;
  color: #fff;
}
.font-jingyong {
  font-size: 24px;
  position: absolute;
  bottom: -6px;
  left: 8px;
  color: crimson;
}
.header-right {
  position: relative;
  cursor: pointer;
}
.content {
  width: 330px;
  min-height: 490px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  @include border();
  @include background_color();
  border-top: none;
}
.content-warp {
  margin-left: 15px;
}
.content-header {
  font-size: 24px;
  font-weight: 600;
  @include font-color();
  line-height: 33px;
  letter-spacing: 1px;
  padding-top: 15px;
}
.content-item-warp {
  margin-top: 13px;
}
.content-item {
  margin-bottom: 21px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.item-left {
  display: flex;
  align-items: center;
}
.item-mark {
  width: 60px;
  height: 27px;
  border-radius: 4px;
  border: 1px solid #fa9d00;
  text-align: center;
  line-height: 27px;
  font-size: 16px;
  font-weight: 500;
  color: #fa9d00;
}
.item-message {
  font-size: 20px;
  font-weight: 600;
  @include font-color();
  margin-left: 15px;
}
.item-name {
  font-size: 20px;
  font-weight: 600;
  @include font-color();
  margin-left: 10px;
}
.item-template {
  font-size: 20px;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  margin-left: 10px;
  color: #fa9d00;
}
.item-right {
  font-size: 18px;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  margin-right: 33px;
}
.red {
  color: rgba(208, 2, 27, 1);
}
.redMark {
  border: 1px solid rgba(208, 2, 27, 1);
  color: rgba(208, 2, 27, 1);
}
.blue {
  color: rgba(63, 201, 255, 1);
}
.blueMark {
  border: 1px solid rgba(63, 201, 255, 1);
  color: rgba(63, 201, 255, 1);
}
.page-warp {
  margin-top: 20px;
  margin-bottom: 15px;
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
.page {
  display: flex;
  margin-right: 33px;
}
.page-left {
  width: 24px;
  height: 24px;
  @include background_color();
  border-radius: 6px;
  border: 1px solid rgba(53, 83, 228, 1);
  line-height: 24px;
  text-align: center;
}
.page-center {
  width: 46px;
  height: 24px;
  line-height: 24px;
  font-size: 22px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  @include font-color();
}
.font-page {
  font-size: 15px;
  color: rgba(53, 83, 228, 1);
  cursor: pointer;
}
</style>



