<template>
  <popUp :flow="ghsbcwStatus">
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="showSet()">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe64d;</i>
          <span class="title-span">{{ $t("monitor.szgz") }}</span>
        </div>
        <div class="mid-change">
          <!-- <div v-for="(item, index) in list" :key="index" @click="choose(item)">
            <div
              class="change-item"
              :class="selectIndex == item ? 'changeCloro' : ''"
            >
              {{ item }}
            </div>
          </div> -->
          <div @click="choose('设备')">
            <div
              class="change-item"
              :class="selectIndex == '设备' ? 'changeCloro' : ''"
            >
              {{ $t("monitor.sb") }}
            </div>
          </div>
          <div @click="choose('床位')">
            <div
              class="change-item"
              :class="selectIndex == '床位' ? 'changeCloro' : ''"
            >
              {{ $t("monitor.cw") }}
            </div>
          </div>
        </div>
        <!-- <div> -->
          <!-- 更换床位 -->
          <ghcw
            v-if="selectIndex == '床位'"
            :transData="transData"
            @closeIitem="closeIitem"
          ></ghcw>
          <!-- 更换设备 -->
          <ghsb
            v-if="selectIndex == '设备'"
            :transData="transData"
            @closeIitem="closeIitem"
          ></ghsb>
          <!-- 修改规则 -->
          <!-- <newXggz
            :transData="transData"
            v-if="selectIndex == '规则'"
            @closeIitem="closeIitem"
          ></newXggz> -->
          <!-- 更换患者 -->
          <!-- <ghhz
            :transData="transData"
            v-if="selectIndex == '患者'"
            @closeIitem="closeIitem"
          ></ghhz> -->
        <!-- </div> -->
        <!-- <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click="chooseBedType()">{{
            $t("button.confirm")
          }}</a>
          <a href="javascript:void(0)" class="off" @click="showSet()">{{
            $t("button.close")
          }}</a>
        </div> -->
      </div>
    </div>
  </popUp>
</template>
<script>
import popUp from "../../components/popUp";
import ghcw from "../monitor/ghcw.vue";
import ghsb from "../monitor/ghsb.vue";
// import ghhz from "../monitor/ghhz.vue";
// import newXggz from "../monitor/newXggz.vue";
export default {
  data() {
    return {
      ghcwStatus: false,
      ghsbStatus: false,
      newXggzStatus: true,
      ghhzStatus: false,
      list: ["设备", "床位"],
      selectIndex: "设备",
    };
  },
  props: ["ghsbcwStatus", "transData"],
  components: {
    popUp,
    ghcw,
    ghsb,
    // ghhz,
    // newXggz,
  },
  methods: {
    showSet() {
      this.$emit("closeIitem", "更换入口");
    },
    choose(data) {
      this.selectIndex = data;
    },
    closeIitem(data) {
      //关闭内容弹框
      this.$emit("closeIitem", "更换入口");
    },
  },
};
</script>
<style scoped lang="scss">
.mid-change {
  display: flex;
  margin-left: 42px;
  margin-bottom: 27px;
  .change-item {
    width: 84px;
    height: 34px;
    border-radius: 17px;
    border: 1px solid #1890ff;
    background: #fff;
    color: #0091ff;
    text-align: center;
    line-height: 34px;
    margin-right: 24px;
    cursor: pointer;
  }
  .changeCloro {
    color: #fff;
    background: #0091ff;
  }
}
</style>



