<template>
  <div>
    <div class="header">
      <i class="iconfont font-shebei">&#xe62e;</i>
      <div class="header-font">{{ $t("monitor.shebei.guaikuang") }}</div>
    </div>
    <div class="content">
      <div class="content-warp">
        <!-- <div class="content-project">
          <div class="project-item" v-if="tiwen">
            <div class="project-name">{{ $t("monitor.shebei.tiwen") }}</div>
            <div class="project-number">
              {{ tiwenNumber }}/{{ tiwen.machineCount }}
            </div>
            <div class="project-active" @click="enableDevice(0)">
              {{ $t("monitor.shebei.qiyong") }}
            </div>
          </div>
          <div class="project-item" v-if="xindian">
            <div class="project-name">{{ $t("monitor.shebei.xindian") }}</div>
            <div class="project-number">
              {{ xindianNumber }}/{{ xindian.machineCount }}
            </div>
            <div class="project-active" @click="enableDevice(1)">
              {{ $t("monitor.shebei.qiyong") }}
            </div>
          </div>
        </div>
        <div class="content-project" v-if="xueyangyi">
          <div class="project-item">
            <div class="project-name">{{ $t("monitor.shebei.xueyang") }}</div>
            <div class="project-number">
              {{ xueyangNumber }}/{{ xueyangyi.machineCount }}
            </div>
            <div class="project-active" @click="enableDevice(2)">
              {{ $t("monitor.shebei.qiyong") }}
            </div>
          </div>
          <div class="project-item" v-if="lichuang">
            <div class="project-name">{{ $t("monitor.shebei.lichuang") }}</div>
            <div class="project-number">
              {{ lichuangNumber }}/{{ lichuang.machineCount }}
            </div>
            <div class="project-active" @click="enableDevice(3)">
              {{ $t("monitor.shebei.qiyong") }}
            </div>
          </div>
        </div> -->
        <div class="select-warp">
          <div class="select-top" @click="changeSelect">
            <div class="select-font">
              {{ selectArr[selectIndex].name }}
              {{ selectArr[selectIndex].number }}/{{
                selectArr[selectIndex].machineCount
              }}
            </div>
            <i class="iconfont select-icon" :class="showSelect ? 'trans' : ''"
              >&#xe625;</i
            >
          </div>
          <div
            class="select-active"
            @click="enableDevice(selectArr[selectIndex].id)"
          >
            {{ $t("monitor.shebei.qiyong") }}
          </div>
          <div class="select-item-warp" v-show="showSelect">
            <div
              class="select-item"
              v-for="(item, index) in selectArr"
              :key="index"
              @click="selectItem(index)"
            >
              {{ item.name }} {{ item.number }}/{{ item.machineCount }}
            </div>
          </div>
        </div>
        <div class="item-warp">
          <div
            class="overview-warp"
            v-for="(item, index) in sheBeiData"
            :key="index"
          >
            <div class="overview-left">
              <div class="overview-mark">{{ item.machineTypeName }}</div>
              <div class="overview-content">
                {{ item.bedName }}&nbsp;&nbsp;{{ item.text }}
              </div>
            </div>
            <div class="overview-right"></div>
          </div>
          <div
            class="overview-warp animated fadeInDown"
            v-for="(items, indexs) in hideData"
            :key="indexs + 'r'"
            v-show="moreData"
          >
            <div class="overview-left">
              <div class="overview-mark">{{ items.machineTypeName }}</div>
              <div class="overview-content">
                {{ items.bedName }}&nbsp;&nbsp;{{ items.text }}
              </div>
            </div>
            <div class="overview-right"></div>
          </div>
          <div class="content-bottom" v-if="sheBeiData.length != 0">
            <i
              class="iconfont font-bottom"
              @click="showData"
              :class="moreData ? 'aa' : 'go'"
              >&#xe625;</i
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      moreData: false,
      showSelect: false,
      selectIndex: 0
    };
  },
  computed: {
    ...mapState("login", ["language"]),
    ...mapState("monitor", ["mainData"]),
    sheBeiData() {
      var arr = this.mainData.machinesInfoVOList.slice(0, 2);
      arr.map((item, index) => {
        if (item.machineType === 0) {
          if (this.language == "en") {
            arr[index].machineTypeName = "Temp ";
            arr[index].text = "power<10%";
          } else {
            arr[index].machineTypeName = "体温贴";
            arr[index].text = "电量不足10%";
          }
        } else if (item.machineType === 1) {
          if (this.language == "en") {
            arr[index].machineTypeName = " ECG   ";
            arr[index].text = "power<10%";
          } else {
            arr[index].machineTypeName = "心电贴 ";
            arr[index].text = "电量不足10%";
          }
        } else if (item.machineType === 2) {
          if (this.language == "en") {
            arr[index].machineTypeName = " SpO2 ";
            arr[index].text = "power<20%";
          } else {
            arr[index].machineTypeName = "血氧";
            arr[index].text = "电量不足20% ";
          }
        } else if (item.machineType === 3) {
          if (this.language == "en") {
            arr[index].machineTypeName = "Bed Tape";
            arr[index].text = "power<10%";
          } else {
            arr[index].machineTypeName = "离床";
            arr[index].text = "电量不足10%";
          }
        } else if (item.machineType === 4) {
          if (this.language == "en") {
            arr[index].machineTypeName = "Temp ";
            arr[index].text = "alter the AAA";
          } else {
            arr[index].machineTypeName = "体温贴";
            arr[index].text = "请更换底座电池";
          }
        }
      });
      return this.mainData.machinesInfoVOList.slice(0, 2);
    },
    hideData() {
      var length = this.mainData.machinesInfoVOList.length;
      console.log(length);
      var arr2 = this.mainData.machinesInfoVOList.slice(2, length);
      arr2.map((item, index) => {
        if (item.machineType === 0) {
          if (this.language == "en") {
            arr2[index].machineTypeName = "Temp ";
            arr2[index].text = "power<10%";
          } else {
            arr2[index].machineTypeName = "体温贴";
            arr2[index].text = "电量不足10%";
          }
        } else if (item.machineType === 1) {
          if (this.language == "en") {
            arr2[index].machineTypeName = " ECG   ";
            arr2[index].text = "power<10%";
          } else {
            arr2[index].machineTypeName = "心电贴 ";
            arr2[index].text = "电量不足10%";
          }
        } else if (item.machineType === 2) {
          if (this.language == "en") {
            arr2[index].machineTypeName = " SpO2 ";
            arr2[index].text = "power<20%";
          } else {
            arr2[index].machineTypeName = "血氧";
            arr2[index].text = "电量不足20% ";
          }
        } else if (item.machineType === 3) {
          if (this.language == "en") {
            arr2[index].machineTypeName = "Bed Tape";
            arr2[index].text = "power<10%";
          } else {
            arr2[index].machineTypeName = "离床";
            arr2[index].text = "电量不足10%";
          }
        } else if (item.machineType === 4) {
          if (this.language == "en") {
            arr2[index].machineTypeName = "Temp ";
            arr2[index].text = "alter the AAA";
          } else {
            arr2[index].machineTypeName = "体温贴";
            arr2[index].text = "请更换底座电池";
          }
        }
      });
      return this.mainData.machinesInfoVOList.slice(2, length);
    },
    // tiwen() {
    //   return this.mainData.machineTypeList.filter((item) => {
    //     return item.id === 0;
    //   })[0];
    // },
    // xindian() {
    //   return this.mainData.machineTypeList.filter((item) => {
    //     return item.id === 1;
    //   })[0];
    // },
    // xueyangyi() {
    //   return this.mainData.machineTypeList.filter((item) => {
    //     return item.id === 2;
    //   })[0];
    // },
    // lichuang() {
    //   return this.mainData.machineTypeList.filter((item) => {
    //     return item.id === 3;
    //   })[0];
    // },
    selectArr() {
      //筛选选择的下拉数组
      var arr = [];
      var linItem;
      this.mainData.machineTypeList.map((item, index) => {
        if (item.id === 0) {
          linItem = item;
          linItem.name = this.$t("monitor.shebei.tiwen");
          linItem.number = this.tiwenNumber;
          arr.push(linItem);
        } else if (item.id === 1) {
          linItem = item;
          linItem.name = this.$t("monitor.shebei.xindian");
          linItem.number = this.xindianNumber;
          arr.push(linItem);
        } else if (item.id === 2) {
          linItem = item;
          linItem.name = this.$t("monitor.shebei.xueyang");
          linItem.number = this.xueyangNumber;
          arr.push(linItem);
        } else if (item.id === 3) {
          linItem = item;
          linItem.name = this.$t("monitor.shebei.lichuang");
          linItem.number = this.lichuangNumber;
          arr.push(linItem);
        }
      });
      return arr;
    },
    tiwenNumber() {
      var number = 0;
      for (var i = 0; i < this.mainData.monitorVOList.length; i++) {
        if (this.mainData.monitorVOList[i].machineList) {
          this.mainData.monitorVOList[i].machineList.map(item => {
            if (item.type == 0 && item.usageState == 1) {
              number = number + 1;
            }
          });
        }
      }
      return number;
    },
    xindianNumber() {
      var number = 0;
      for (var i = 0; i < this.mainData.monitorVOList.length; i++) {
        if (this.mainData.monitorVOList[i].machineList) {
          this.mainData.monitorVOList[i].machineList.map(item => {
            if (item.type == 1 && item.usageState == 1) {
              number = number + 1;
            }
          });
        }
      }
      return number;
    },
    xueyangNumber() {
      var number = 0;
      for (var i = 0; i < this.mainData.monitorVOList.length; i++) {
        if (this.mainData.monitorVOList[i].machineList) {
          this.mainData.monitorVOList[i].machineList.map(item => {
            if (item.type == 2 && item.usageState == 1) {
              number = number + 1;
            }
          });
        }
      }
      return number;
    },
    lichuangNumber() {
      var number = 0;
      for (var i = 0; i < this.mainData.monitorVOList.length; i++) {
        if (this.mainData.monitorVOList[i].machineList) {
          this.mainData.monitorVOList[i].machineList.map(item => {
            if (item.type == 3 && item.usageState == 1) {
              number = number + 1;
            }
          });
        }
      }
      return number;
    }
  },
  //   mounted() {
  //     this.selectData = this.selectArr[0];
  //     console.log(this.selectData);
  //   },
  methods: {
    showData() {
      this.moreData = !this.moreData;
    },
    changeSelect() {
      this.showSelect = !this.showSelect;
    },
    selectItem(index) {
      //选中了下拉的某一项
      this.showSelect = false;
      this.selectIndex = index;
    },
    enableDevice(data) {
      //启动设备
      var name = "";
      var arr = this.mainData.machineTypeList.filter(item => {
        if (data === 0) {
          name = "体温贴";
        } else if (data === 1) {
          name = "心电贴";
        } else if (data === 2) {
          name = "血氧仪";
        } else if (data === 3) {
          name = "离床感应";
        }
        return item.id === data;
      });
      this.$emit("setDevice", name, arr[0]);
    }
  }
};
</script>
<style scoped lang="scss">
@mixin font-color() {
  color: rgba(255, 255, 255, 0.85);
  [data-theme="black"] & {
    color: rgba(255, 255, 255, 0.85);
  }
  [data-theme="white"] & {
    color: rgba(24, 76, 153, 1);
  }
}
@mixin background_color() {
  [data-theme="white"] & {
    background: rgba(248, 251, 255, 1);
  }
}
@mixin background_color2() {
  background: #0e2649;
  [data-theme="black"] & {
    background: #0e2649;
  }
  [data-theme="white"] & {
    background: rgba(248, 251, 255, 1);
  }
}
@mixin background_color3() {
  background: rgba(7, 34, 74, 1);
  [data-theme="black"] & {
    background: rgba(7, 34, 74, 1);
  }
  [data-theme="white"] & {
    background: rgba(19, 74, 155, 1);
  }
}
@mixin background_color4() {
  background: #071d3d;
  [data-theme="black"] & {
    background: #071d3d;
  }
  [data-theme="white"] & {
    background: rgba(217, 228, 242, 1);
  }
}
@mixin border() {
  border: 1px solid rgba(9, 76, 138, 1);
  [data-theme="black"] & {
    border: 1px solid rgba(9, 76, 138, 1);
  }
  [data-theme="white"] & {
    border: none;
  }
}
@mixin border2() {
  border: 1px solid rgba(9, 76, 138, 1);
  [data-theme="black"] & {
    border: 1px solid rgba(9, 76, 138, 1);
  }
  [data-theme="white"] & {
    border: 1px solid #c3daf6;
  }
}
@mixin border-bottom() {
  border-bottom: 1px solid rgba(41, 58, 105, 1);
  [data-theme="black"] & {
    border-bottom: 1px solid rgba(41, 58, 105, 1);
  }
  [data-theme="white"] & {
    border-bottom: 1px solid rgba(217, 231, 250, 1);
  }
}
.header {
  width: 330px;
  height: 40px;
  @include background_color3();
  box-shadow: 1px 1px 5px 0px rgba(53, 83, 228, 1);
  border-radius: 5px 5px 0px 0px;
  border: 1px solid rgba(53, 83, 228, 1);
  display: flex;
  align-items: center;
}
.font-shebei {
  color: rgba(63, 201, 255, 1);
  font-size: 20px;
  margin-left: 28px;
}
.font-bottom {
  font-size: 9px;
  @include font-color();
  cursor: pointer;
}
.header-font {
  font-size: 18px;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  line-height: 25px;
  letter-spacing: 1px;
  margin-left: 16px;
}
.select-warp {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .select-active {
    color: #3fc9ff;
    font-size: 18px;
    margin-right: 21px;
    cursor: pointer;
  }
}
.select-top {
  width: 200px;
  min-height: 36px;
  border: 1px solid #0a4680;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.select-font {
  font-size: 18px;
  font-weight: 500;
  @include font-color();
  line-height: 25px;
  margin-left: 10px;
}
.select-icon {
  font-size: 8px;
  color: #0a4680;
  margin-right: 13px;
}
.select-item-warp {
  max-height: 220px;
  overflow: auto;
  width: 200px;
  position: absolute;
  @include border2();
  border-radius: 4px;
  top: 50px;
  @include background_color2();
  z-index: 10;
  .select-item {
    height: 52px;
    @include border-bottom();
    line-height: 52px;
    text-indent: 10px;
    @include font-color();
    font-size: 16px;
    cursor: pointer;
  }
  .select-item:last-of-type {
    border: none;
  }
  .select-item:hover {
    @include background_color4();
  }
}
.trans {
  transition: all 1s;
  transform: rotate(-180deg);
}
.content {
  min-height: 198px;
  width: 330px;
  min-height: 198px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  @include border();
  border-top: none;
  position: relative;
  @include background_color();
}
.content-warp {
  margin-left: 15px;
  padding-bottom: 38px;
}
.content-project {
  display: flex;
  width: 450px;
  justify-content: space-between;
}
.content-bottom {
  margin-top: 14px;
  margin-bottom: 11px;
  display: flex;
  justify-content: center;
  width: 100%;
  position: absolute;
  bottom: 2px;
  left: 0px;
}
.project-item {
  display: flex;
  margin-top: 13px;
  align-items: center;
}
.project-name {
  font-size: 20px;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 28px;
}
.project-number {
  font-size: 20px;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 28px;
  margin-left: 13px;
}
.project-active {
  font-size: 20px;
  font-weight: 400;
  color: rgba(63, 201, 255, 1);
  line-height: 28px;
  margin-left: 13px;
  margin-right: 15px;
  cursor: pointer;
}
.overview-warp {
  display: flex;
  justify-content: space-between;
  margin-top: 13px;
}
.overview-left {
  display: flex;
}
.overview-mark {
  min-height: 27px;
  border-radius: 4px;
  border: 1px solid rgba(245, 166, 35, 1);
  line-height: 27px;
  font-size: 16px;
  color: rgba(245, 166, 35, 1);
  text-align: center;
  padding: 0 5px;
  min-width: 60px;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.overview-content {
  width: 220px;
  font-size: 20px;
  @include font-color();
  line-height: 28px;
  margin-left: 15px;
  overflow: hidden; //超出的文本隐藏
  text-overflow: ellipsis; //溢出用省略号显示
  white-space: nowrap; //溢出不换行
}
.overview-right {
  font-size: 18px;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 25px;
}
.aa {
  transition: all 1s;
}
.go {
  transform: rotate(-180deg);
  transition: all 1s;
}
.item-warp {
  max-height: 230px;
  overflow: auto;
}
::-webkit-scrollbar {
  width: 7px;
  background: rgba(255, 255, 255, 0.16);
  border-radius: 7px;
}
::-webkit-scrollbar-thumb {
  width: 2px;
  background: rgba(255, 255, 255, 0.16);
  border-radius: 7px;
}
</style>



